import React, { useEffect } from "react";
import "../App.css";
import "../styles/base.css";

const SettingsPanel = ({ selectedCategories, setSelectedCategories }) => {
  const handleCategoryChange = (category) => {
    setSelectedCategories((prev) => ({
      ...prev,
      [category]: !prev[category] // 기존 값 토글 (ON/OFF)
    }));
  };

  const categoryLabels = {
    main: "메인",
    info: "정보",
    other: "잡담"
  };

  // 📌 기존 선택값 유지 + 새로운 카테고리만 `false`로 추가
  const updatedCategories = { ...selectedCategories };
  Object.keys(categoryLabels).forEach((key) => {
    if (!(key in updatedCategories)) {
      updatedCategories[key] = false;
    }
  });

  return (
    <div>
      <div className="skinTypeCheck">
        <h4>02. 출력 탭 선택 <b>(*중복 선택 가능)</b></h4>
        <ul>
          {Object.keys(updatedCategories).map((category) => (
            <li key={category}>
              <input
                type="checkbox"
                id={category}
                value={category}
                checked={updatedCategories[category]}
                onChange={() => handleCategoryChange(category)}
              />
              <label htmlFor={category}>{categoryLabels[category] || category}</label>
            </li>
          ))}
        </ul>
      </div>
    </div>
  );
};


export default SettingsPanel;
